export interface Position2D {
    x: number;
    y: number;
}

export interface Position3D {
    x: number;
    y: number;
    z: number;
}

export interface Size2D {
    width: number;
    height: number;
}

export interface Size3D {
    width: number;
    height: number;
    depth: number;
}

export function degreesToRadians(degrees: number) {
    return degrees * (Math.PI / 180);
}